class SimpleSearch extends React.Component {

  constructor (props) {
    super(props);
    this.state = {
      search: '',
      activePanel: 'add',

    }
    this.onChange = this.onChange.bind(this);
  }

  onChange (search) { this.setState({ search }); }

  get thematics () {
    const search = this.state.search.toLowerCase();
    return places.filter(({title}) => title.toLowerCase().indexOf(search) > -1);
  }

  render() {
    return (
      <Panel id={this.props.id}>
        <PanelHeader
          noShadow
          left={<HeaderButton onClick={this.props.go} data-to="way">
            {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
          </HeaderButton>}>
           
        </PanelHeader>
        <Search value={this.state.search} onChange={this.onChange}/>
        {this.thematics.length > 0 &&
          <List>
            {this.thematics.map(thematic =>

              <Cell
                  size="l"
                  bottomContent={
                    <div style={{ display: 'flex' }}>
                      <Button size="m" onClick={this.props.go} data-to="way"></Button>
                      <Button size="m" level="secondary" style={{ marginLeft: 8 }} onClick={this.props.player}></Button>
                    </div>
                  }
                >
                  {thematic.title}</Cell>)}


          </List>
        }
      </Panel>
    );
  }
}


export default SimpleSearch;
